let that;
let uid;
let goodsId;

export default {
	components: {

	},
	data() {
		return {
			seleted_tab: 1,
			details: null,
			tabBars: [{
				name: '入库',
				type: 1
			}, {
				name: '出库',
				type: -1
			}, {
				name: '盘点',
				type: 3
			}, {
				name: '退货',
				type: 2
			}, ]
		}
	},
	onLoad(options) {
		that = this;
		uid = wx.getStorageSync("uid");
		goodsId = options.objectId;
		that.getdetail();
	},
	methods: {
		tapTab: function(type) {
			that.seleted_tab = type
			that.getdetail()
		},

		getdetail() {
			uni.showLoading({
				title: "加载中..."
			})
			const query = Bmob.Query("Bills");
			query.equalTo("userId", "==", uid);
			query.equalTo("type", "==", that.seleted_tab);
			query.equalTo("goodsId", "==", goodsId);
			query.order("-createdAt")
			query.find().then(res => {
				console.log(res)
				that.details = res;
				uni.hideLoading();
			});
		}
	}
}
